<template>
  <div>
    <form @submit.prevent="addEditUser">
      <!-- text -->
      <div class="fieldset-div">
        <fieldset>
          <label for="user_first_name">
            Imię
            <input id="user_first_name" v-model="user_data.firstName"/>
          </label>
        </fieldset>
        <fieldset>
          <label for="user_last_name">
            Nazwisko
            <input id="user_last_name" type="text" name="user_last_name" v-model="user_data.lastName">
          </label>
        </fieldset>
      </div>
      <div class="fieldset-div">
        <fieldset>
          <label for="user_login">
            Login
            <input id="user_login" type="text" name="user_login" v-model="user_data.login">
          </label>
        </fieldset>
        <fieldset>
          <label for="user_phone">
            Telefon (kom)
            <input id="user_phone" v-model="user_data.phone" />
          </label>
        </fieldset>
      </div>
      <div class="fieldset-div">
        <fieldset>
          <label for="user_email">
            Email
            <input id="user_email" type="text" name="user_email" v-model="user_data.email">
          </label>
        </fieldset>
      </div>
      <div class="fieldset-div">
        <fieldset>
          <label for="user_password">
            Hasło
            <input id="user_password" type="text" name="user_password" v-model="user_data.password">
          </label>
        </fieldset>
        <fieldset>
          <label for="user_password_repeat">
            Powtórz hasło
            <input id="user_password_repeat" type="text" name="user_password_repear" v-model="user_data.password_repeat">
          </label>
        </fieldset>
      </div>
      <div class="fieldset-div">
        <fieldset class="button-fieldset">
          <button  name="submit" type="submit" id="user-add" data-submit="...Sending" v-if="action === 'add'">Dodaj</button>
          <button  name="submit" type="submit" id="user-edit" data-submit="...Sending" v-if="action !== 'add'">Zapisz</button>
        </fieldset>
      </div>
    </form>
  </div>
</template>

<script>
// import History from '../../../../../model/History';
// import Api from '../../../../../model/Api';
// import store from '../../../../../store';

export default {
  name: 'TankForm',
  props: ['formData', 'action'],
  data() {
    return {
    //   user: this.$session.get('user_info'),
      user_data: {
        firstName: '',
        lastName: '',
        login: '',
        phone: '',
        email: '',
        password: undefined,
        password_repeat: undefined,
      }
    };
  },
  async created() {
    // const self = this;
    if(this.action === 'edit') {
      this.user_data = this.formData;
    }
  },
  methods: {
    addEditUser() {
    //   const self = this;
      const data = {
        user_first_name: this.user_data.firstName,
        user_last_name: this.user_data.lastName,
        user_login: this.user_data.login,
        user_phone: this.user_data.phone,
        user_email: this.user_data.email,
        user_password: this.user_data.password,
        user_password_repeat: this.user_data.password_repeat,
      };
      if (this.action !== 'add') {
        data.id = this.formData.id;
        // store.dispatch('adminPanel/editUser', data);
      } else {
        // store.dispatch('adminPanel/addUser', data);
      }
    },
  },
  computed: {
  },
};
</script>

<style lang="scss">
  .v-modal {
    padding: 20px;
  }

</style>
